.intro {
  width: 100%;
  padding: 20px;
}
.intro h1 {
  line-height: normal;
}
.intro a {
  color: #ee75ab;
  cursor: pointer;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Nyashs";
  font-display: swap;
  src: url("../fonts/nyasha-sans-regular.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
  user-select: none;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Nyashs", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  background-color: #ffffff;
  font-family: "Nyashs", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  text-align: left;
  color: #000000;
  scroll-behavior: smooth;
}

footer {
  margin-top: auto;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  height: auto;
  width: 100%;
  object-fit: cover;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

.simple-cursor {
  width: 20px;
  height: 20px;
  top: -10px;
  left: -10px;
  border-radius: 50%;
  position: fixed;
  display: flex;
  z-index: 9999;
  transition: transform 0.05s, z-index 0.05s 0.1s;
  pointer-events: none;
}
.simple-cursor span {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transition: transform 0.6s ease;
  background-color: #ff69ca;
}
.simple-cursor.is-hidden {
  z-index: -1;
}
.simple-cursor.is-hidden span {
  transform: scale(0);
  transition: transform 0.2s ease;
}

.header {
  width: 100%;
  background-color: #ffffff;
  position: sticky;
  top: 0;
  z-index: 2;
}

.header__wrapper {
  min-height: 104px;
  min-width: 320px;
  max-width: 1440px;
  padding: 0 40px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (max-width: 1439px) {
  .header__wrapper {
    padding: 10px 80px;
  }
}
@media (max-width: 999px) {
  .header__wrapper {
    padding: 10px 25px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 0;
    min-height: 98px;
    justify-content: space-between;
  }
}

.header__sticky-wrap {
  padding: 0;
  min-width: unset;
  max-width: 136px;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 767px) {
  .header__sticky-wrap {
    min-width: 320px;
    max-width: 100%;
    min-height: 98px;
    display: flex;
    background-color: #ffffff;
    padding: 10px 20px;
    border-bottom: 2px solid #000000;
    z-index: 9999999999;
  }
}

.header__logo {
  max-width: 136px;
  max-height: 100px;
  cursor: pointer;
  margin: 0;
}
@media (max-width: 767px) {
  .header__logo {
    margin: -34px 0 0 -20px;
    z-index: 9999999999;
  }
}
.header__logo:hover, .header__logo:focus {
  opacity: 0.8;
}

.header__subwrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 0 0 40px;
  background-color: #ffffff;
}
@media (max-width: 1439px) {
  .header__subwrapper {
    margin: 0 0 0 35px;
  }
}
@media (max-width: 767px) {
  .header__subwrapper {
    display: none;
    flex-direction: column;
    margin: 0;
    max-width: 382px;
    max-height: 572px;
    flex-shrink: 0;
  }
}
@media (max-width: 479px) {
  .header__subwrapper {
    max-height: 500px;
  }
}

.header__button {
  max-width: 177px;
  height: fit-content;
  border: 2px solid #000000;
  box-shadow: 4px 4px 0 0 #000000;
  background-color: #ffe249;
  padding: 16px 32px;
  font-size: 18px;
  color: #000000;
  cursor: pointer;
}
@media (max-width: 767px) {
  .header__button {
    max-width: 239px;
    font-size: 28px;
    padding: 14px 42px;
    margin-bottom: 35px;
    z-index: 9999999999;
  }
}
@media (max-width: 479px) {
  .header__button {
    max-width: 193px;
    font-size: 20px;
  }
}
.header__button:hover, .header__button:focus {
  transform: scale(1.05);
  border: 2px solid #f668a2;
  box-shadow: 4px 4px 0 0 #f668a2;
  color: #f668a2;
}

.header__social {
  display: none;
}
@media (max-width: 767px) {
  .header__social {
    display: flex;
  }
}

.header__subwrapper.is-opened {
  display: flex;
  overflow-y: auto;
  height: 600px;
  max-height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1;
  background-image: url("./../img/dots/background-dot-black.webp");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  padding: 163px 45px 18px 34px;
  border-top: 2px solid #000000;
  border-right: 4px solid #000000;
  border-bottom: 6px solid #000000;
  border-left: 2px solid #000000;
}

.header.is-opened::before {
  content: "";
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  inset: 0;
  transform: scale(1);
}

.nav {
  align-self: center;
}

.nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 40px;
}
@media (max-width: 767px) {
  .nav__list {
    flex-direction: column;
    gap: 36px;
    margin-bottom: 44px;
  }
}

.nav__item {
  font-size: 20px;
  cursor: pointer;
}
@media (max-width: 767px) {
  .nav__item {
    font-size: 32px;
    background-color: #ffffff;
    width: fit-content;
  }
}
@media (max-width: 479px) {
  .nav__item {
    font-size: 26px;
  }
}
.nav__item:hover, .nav__item:focus {
  transform: scale(1.05);
}
.nav__item:hover .nav__link, .nav__item:focus .nav__link {
  color: #f668a2;
}

@media (min-width: 768px) and (max-width: 1000px) {
  .nav__item--hidden {
    display: none;
    width: 0;
  }
}

.toggle {
  display: none;
}
@media (max-width: 767px) {
  .toggle {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    outline: none;
    cursor: pointer;
    border: none;
    background-color: transparent;
    z-index: 9999999999;
  }
}
@media (max-width: 479px) {
  .toggle {
    justify-content: end;
  }
}
.toggle:hover, .toggle:focus {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .toggle__opened,
.toggle__closed {
    width: 70%;
  }
}

.toggle__opened {
  margin-right: -10px;
}

.toggle .toggle__closed {
  display: none;
  opacity: 0;
}

.toggle.is-opened .toggle__opened {
  display: none;
}

.toggle.is-opened .toggle__closed {
  display: inline;
  opacity: 1;
  margin-right: -5px;
}

.social {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (max-width: 479px) {
  .social__link img {
    width: 90%;
  }
}
.social__link img:hover, .social__link img:focus {
  opacity: 0.9;
  transform: scale(1.05);
}

.footer {
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
}

.footer__wrapper {
  padding: 40px 100px 44px 87px;
  display: grid;
  grid-template-areas: "C N F S" "C N F L";
  grid-template-columns: 293px 214px 500px auto;
  justify-content: center;
}
@media (max-width: 1439px) {
  .footer__wrapper {
    padding: 34px 10px 32px 37px;
    grid-template-areas: "C N S" "C F L";
    grid-template-columns: 306px 470px auto;
  }
}
@media (max-width: 999px) {
  .footer__wrapper {
    padding: 25px 10px 7px 7px;
    grid-template-areas: "C N S" "C N L" "F F L";
    grid-template-columns: 290px 266px auto;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    padding: 10px 8px 5px 13px;
    grid-template-areas: "L N " "L S" "F F" "C C";
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 479px) {
  .footer__wrapper {
    grid-template-areas: "L N" "S S" "C C";
  }
}

.footer__infoCompany {
  grid-area: C;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1439px) {
  .footer__infoCompany {
    margin: 0 0 0 12px;
  }
}
@media (max-width: 767px) {
  .footer__infoCompany {
    display: grid;
    grid-template-columns: 57% 43%;
    column-count: 2;
    margin: 0 auto;
  }
}
@media (max-width: 479px) {
  .footer__infoCompany {
    column-count: 1;
    grid-template-columns: 1fr;
    margin: unset;
  }
}

.footer__company {
  margin: 0 0 20px;
}
.footer__company span {
  margin: 0;
  padding: 0;
  font-size: 16px;
}
.footer__company p {
  white-space: pre-line;
  margin: 0;
  padding: 0;
  font-size: 10px;
  line-height: 17px;
}
@media (max-width: 1439px) {
  .footer__company {
    padding: 6px 0 0;
  }
}
@media (max-width: 999px) {
  .footer__company {
    margin: 0 0 2px;
  }
}
@media (max-width: 767px) {
  .footer__company {
    padding: 27px 0 0;
    margin: 0;
  }
}
@media (max-width: 479px) {
  .footer__company {
    margin: 0 0 20px;
  }
}

.footer__paschalka {
  color: #000000;
  padding: 0;
  margin: 0;
  cursor: pointer;
}
.footer__paschalka:hover, .footer__paschalka:focus {
  color: #f668a2;
}

.footer__link {
  display: flex;
  flex-direction: column;
  margin: 0 0 6px;
  padding: 0;
  font-size: 10px;
  line-height: normal;
}
.footer__link a {
  margin: 0 0 3px;
}
.footer__link a:hover, .footer__link a:focus {
  color: #f668a2;
}
@media (max-width: 1439px) {
  .footer__link {
    padding: 3px 0 0;
    max-width: 230px;
  }
}
@media (max-width: 999px) {
  .footer__link {
    padding: 3px 0 0;
    max-width: 235px;
  }
}
@media (max-width: 767px) {
  .footer__link {
    justify-content: flex-end;
    margin: 0;
  }
