.footer__contacts {
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 10px;
  margin: 0;
  padding: 0;
  font-size: 10px;
  line-height: normal;
}
.footer__contacts a:hover, .footer__contacts a:focus {
  color: #f668a2;
}
@media (max-width: 999px) {
  .footer__contacts {
    gap: 0;
  }
}
@media (max-width: 767px) {
  .footer__contacts {
    margin: 13px 0 0;
  }
}

.footer__nav {
  grid-area: N;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  line-height: normal;
}
@media (max-width: 479px) {
  .footer__nav {
    font-size: 16px;
  }
}

.footer__subscription {
  grid-area: F;
  align-self: end;
  padding: 0 0 33px;
}
@media (max-width: 1439px) {
  .footer__subscription {
    align-self: start;
    padding: 10px 0 0;
    margin: 0;
  }
}
@media (max-width: 999px) {
  .footer__subscription {
    align-self: center;
  }
}

.footer__social {
  grid-area: S;
  display: flex;
  flex-direction: column;
  max-width: 225px;
  padding: 0 0 0 43px;
}
.footer__social span {
  font-size: 20px;
  line-height: normal;
  margin: -2px 0 20px 6px;
}
.footer__social ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  gap: 9px;
}
@media (max-width: 1439px) {
  .footer__social {
    padding: 0;
    margin: -16px 0 0;
    max-width: 182px;
  }
}
@media (max-width: 999px) {
  .footer__social {
    margin: 0;
  }
  .footer__social span {
    margin: 7px 0 14px 6px;
  }
  .footer__social ul {
    max-width: 128px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .footer__social {
    margin: 0 0 24px;
    max-width: 100%;
  }
  .footer__social span {
    margin: -2px 0 19px 7px;
  }
  .footer__social ul {
    max-width: 161px;
    margin: 0 0 0 10px;
  }
}

.footer__social-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.footer__social-link:hover, .footer__social-link:focus {
  opacity: 0.9;
}

.footer__logo {
  grid-area: L;
  width: 244px;
