.control {
  position: relative;
  padding-left: 25px;
  cursor: pointer;
}

.control__mark {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #000000;
  background: #ffffff;
  border-radius: 50%;
}

.control__customLabel {
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #000000;
}
@media (max-width: 767px) {
  .control__customLabel {
    font-size: 14px;
  }
}

/*  hover */
.control__input[type=radio]:hover + .control__mark {
  border: 1px solid #f668a2;
  background: #ffffff;
}

.control__input[type=radio]:hover + .control__mark::before {
  background-color: #f668a2;
}

.control__input[type=radio]:hover ~ .control__customLabel {
  color: #f668a2;
}

/*  checked */
.control__input[type=radio]:checked + .control__mark::before {
  position: absolute;
  content: "";
  top: 2px;
  left: 2px;
  width: 14px;
  height: 14px;
  background-color: #000000;
  border-radius: 50%;
}

.control__input[type=radio]:checked:hover + .control__mark {
  border: 1px solid #000000;
  background: #ffffff;
  cursor: not-allowed;
}

.control__input[type=radio]:checked:hover ~ .control__customLabel {
  color: #000000; /*     */
}

